	module.exports = {"data":{"uiComponentsYaml":{"title":"Dropdowns","category":"UI components","examples":[{"title":"Usage","introduction":"The state of the component can be managed externally via the `isOpen` property.","subTitle":"Example","react":"import React from 'react';\nimport { Dropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';\n\nexport default class Example extends React.Component {\n  constructor(props) {\n    super(props);\n\n    this.toggle = this.toggle.bind(this);\n    this.state = {\n      dropdownOpen: false\n    };\n  }\n\n  toggle() {\n    this.setState({\n      dropdownOpen: !this.state.dropdownOpen\n    });\n  }\n\n  render() {\n    return (\n      <Dropdown isOpen={this.state.dropdownOpen} toggle={this.toggle}>\n        <DropdownToggle caret>\n          Dropdown button\n        </DropdownToggle>\n        <DropdownMenu>\n          <DropdownItem>Action</DropdownItem>\n          <DropdownItem>Another Action</DropdownItem>\n          <DropdownItem>Something else here</DropdownItem>\n        </DropdownMenu>\n      </Dropdown>\n    );\n  }\n}\n","css":null,"html":null,"actualHtml":null},{"title":"Uncontrolled component","introduction":"The uncontrolled variant of the component manages its own state.","subTitle":"Example","react":"import React from 'react';\nimport { UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem } from 'reactstrap';\n\nexport default function Example () {\n  return (\n    <UncontrolledDropdown>\n      <DropdownToggle caret>\n        Dropdown button\n      </DropdownToggle>\n      <DropdownMenu>\n        <DropdownItem>Action</DropdownItem>\n        <DropdownItem>Another Action</DropdownItem>\n        <DropdownItem>Something else here</DropdownItem>\n      </DropdownMenu>\n    </UncontrolledDropdown>\n  );\n}\n","css":null,"html":null,"actualHtml":null},{"title":"Usage","introduction":null,"subTitle":"Example","react":null,"css":null,"html":"<div class=\"dropdown\">\n  <button\n    class=\"btn btn-secondary dropdown-toggle\"\n    type=\"button\"\n    id=\"dropdownMenuButton\"\n    data-toggle=\"dropdown\"\n    aria-haspopup=\"true\"\n    aria-expanded=\"false\"\n  >\n    Dropdown button\n  </button>\n  <div class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton\">\n    <a class=\"dropdown-item\" href=\"#\">Action</a>\n    <a class=\"dropdown-item\" href=\"#\">Another action</a>\n    <a class=\"dropdown-item\" href=\"#\">Something else here</a>\n  </div>\n</div>\n","actualHtml":null},{"title":"Usage","introduction":null,"subTitle":"Example","react":null,"css":null,"html":"<div class=\"dropdown\">\n  <button\n    class=\"btn btn-secondary\"\n    type=\"button\"\n    id=\"dropdownMenuButton\"\n    data-toggle=\"dropdown\"\n    aria-haspopup=\"true\"\n    aria-expanded=\"false\"\n  >\n    <i class=\"material-icons md-18 align-top\">more_horiz</i>\n  </button>\n  <div class=\"dropdown-menu\" aria-labelledby=\"dropdownMenuButton\">\n    <a class=\"dropdown-item\" href=\"#\">Action</a>\n    <a class=\"dropdown-item\" href=\"#\">Another action</a>\n    <a class=\"dropdown-item\" href=\"#\">Something else here</a>\n  </div>\n</div>\n","actualHtml":null}],"fields":{"slug":"/ui-components/dropdowns/","preHtml":"<p>This component presents a dropdown that is controlled by a toggle button. It is compatible with screen readers and keyboard navigation.</p>\n","postHtml":"\n"}}},"pathContext":{"slug":"/ui-components/dropdowns/"}}

